"use client";
import { ReactNode, useState } from 'react';
import { StaticImageData } from 'next/image';
import BackgroundOverlay from './BackgroundOverlay';
import ScrollIndicator from './ScrollIndicator';

interface HomePageLayoutProps {
  children: ReactNode;
  backgroundImage: StaticImageData;
}

export default function HomePageLayout({ children, backgroundImage }: HomePageLayoutProps) {
  const [mode, setMode] = useState<'light' | 'dark'>('light');

  return (
    <section
      className={`min-h-screen bg-cover bg-center bg-no-repeat relative overflow-hidden transition-colors duration-500 ${mode === 'dark' ? 'dark' : ''}`}
      style={{
        backgroundImage: `url(${backgroundImage.src})`
      }}
    >
      {/* Overlay for contrast */}
      <div className="absolute inset-0 bg-black/30 dark:bg-black/60 z-0 pointer-events-none" />

      <BackgroundOverlay />

      {/* Mode Toggle Button */}
      <button
        className="absolute top-6 right-6 z-20 bg-white/80 dark:bg-[#222]/80 text-[#005649] dark:text-white rounded-full px-4 py-2 shadow-lg font-semibold transition-all hover:scale-105"
        onClick={() => setMode(mode === 'light' ? 'dark' : 'light')}
        aria-label="Toggle light/dark mode"
      >
        {mode === 'light' ? 'üåô Mode sombre' : '‚òÄÔ∏è Mode clair'}
      </button>

      {/* Top Navigation Spacer */}
      <div className="h-16 md:h-20"></div>

      {/* Main Content Container */}
      <div className="relative z-10 flex flex-col items-center justify-center min-h-[70vh] px-2 sm:px-6">
        <div className="w-full max-w-5xl bg-white/80 dark:bg-[#222]/80 backdrop-blur-md rounded-2xl shadow-2xl p-4 sm:p-8 mt-4 animate-fade-in">
          {children}
        </div>
      </div>

      {/* Scroll Indicator always on top */}
      <div className="absolute bottom-4 left-1/2 -translate-x-1/2 z-20">
        <ScrollIndicator />
      </div>

      {/* Fade-in animation */}
      <style jsx global>{`
        .animate-fade-in {
          animation: fadeIn 1.2s cubic-bezier(0.4,0,0.2,1);
        }
        @keyframes fadeIn {
          from { opacity: 0; transform: translateY(32px);}
          to { opacity: 1; transform: translateY(0);}
        }
      `}</style>
    </section>
  );
}